@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-backface-visibility: hidden;
  -webkit-user-drag: none;
  /*font-family: "Microsoft YaHei", "微软雅黑", Helvetica, Tahoma,Arial, SimSun, "宋体", STXihei, "华文细黑", Heiti, "黑体", sans-serif;*/
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  /* 字体抗锯齿 */
  cursor: pointer; }

html, body {
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif; }

a {
  text-decoration: none; }

em {
  font-style: normal; }

input, textarea {
  margin: 0;
  padding: 0;
  border: none;
  outline: none; }

input[type=number] {
  -moz-appearance: textfield; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset; }

::-webkit-input-placeholder, ::-webkit-textarea-placeholder {
  margin: 0;
  padding: 0;
  color: #8c8c8c; }

@media screen and (min-width: 1600px) {
  html, body {
    font-size: 22px; } }

@media screen and (min-width: 1400px) and (max-width: 1600px) {
  html, body {
    font-size: 20px; } }

@media screen and (min-width: 1200px) and (max-width: 1400px) {
  html, body {
    font-size: 18px; } }

@media screen and (min-width: 992px) and (max-width: 1200px) {
  html, body {
    font-size: 17px; } }

@media screen and (min-width: 768px) and (max-width: 992px) {
  html, body {
    font-size: 17px; } }

@media screen and (max-width: 768px) {
  html, body {
    font-size: 17px; } }

/* 定义适配基础单位 */
/* 定义px转rem规则 */
/* 定义间距尺寸 */
:root {
  --head-top: #f5f5f5;
  --head-tab: #ffffff;
  --head-name: #141414;
  --head-tips: #262626; }

.fiscal-box {
  margin-top: 1.75rem;
  overflow: hidden; }
  .fiscal-box .fiscal-head {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.125rem; }
    .fiscal-box .fiscal-head .fiscal-item {
      width: 18.75rem; }
      .fiscal-box .fiscal-head .fiscal-item .fiscal-image {
        width: 18.75rem; }
      .fiscal-box .fiscal-head .fiscal-item .fiscal-text {
        padding: 0.375rem 0.75rem 0.875rem 0.75rem;
        background-color: #F0F4FD; }
        .fiscal-box .fiscal-head .fiscal-item .fiscal-text .fiscal-title {
          display: flex;
          justify-content: space-between;
          align-items: center; }
          .fiscal-box .fiscal-head .fiscal-item .fiscal-text .fiscal-title h6 {
            margin-top: 0.5rem;
            font-size: 0.75rem;
            font-weight: 500;
            color: #434343;
            text-align: left; }
          .fiscal-box .fiscal-head .fiscal-item .fiscal-text .fiscal-title em {
            margin-top: 0.5rem;
            font-size: 0.75rem;
            font-weight: 400;
            color: #262626;
            color: #cf1322; }
          .fiscal-box .fiscal-head .fiscal-item .fiscal-text .fiscal-title em::after {
            content: '';
            display: inline-block;
            vertical-align: 1px;
            width: 6px;
            height: 6px;
            border-left: 1px solid #cf1322;
            border-bottom: 1px solid #cf1322;
            transform: matrix(-0.71, -0.71, 0.71, -0.71, 0, 0);
            margin-left: 5px; }
        .fiscal-box .fiscal-head .fiscal-item .fiscal-text span {
          display: inline-block;
          margin-top: 0.5rem;
          font-size: 0.75rem;
          font-weight: 400;
          color: #262626; }
    .fiscal-box .fiscal-head .fiscal-item:hover {
      transform: translateY(-5px);
      transition: ease all 0.3s;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.09); }
    .fiscal-box .fiscal-head .fiscal-choose {
      width: 20rem; }
  .fiscal-box .fiscal-body {
    margin: 0 auto;
    margin-top: 1.75rem;
    display: flex;
    flex-direction: row;
    width: 57.5rem; }
    .fiscal-box .fiscal-body .fiscal-query {
      margin-bottom: 1.75rem;
      position: relative;
      display: flex;
      flex: 1;
      border: 1px solid #d9d9d9; }
      .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt {
        margin-top: 1.125rem; }
        .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt .fiscal-label {
          margin-left: 1.75rem;
          margin-top: 1.125rem; }
          .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt .fiscal-label label {
            display: inline-block;
            font-size: 0.875rem;
            font-weight: 400;
            color: #141414;
            width: 7.375rem; }
          .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt .fiscal-label input {
            padding: 0.0625rem 0;
            font-size: 0.875rem;
            font-weight: 400;
            color: #141414;
            width: 11.75rem;
            border-bottom: 1px solid #d9d9d9; }
          .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt .fiscal-label .fiscal-select {
            display: inline-block; }
            .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt .fiscal-label .fiscal-select span {
              padding: 0.125rem 0.25rem;
              margin-right: 1.125rem;
              font-size: 0.75rem;
              font-weight: 400;
              color: #262626; }
            .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt .fiscal-label .fiscal-select .fiscal-check {
              color: #ffffff;
              background-color: #cf1322; }
        .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt .fiscal-result {
          margin-top: 1.125rem;
          margin-left: 1.75rem;
          font-size: 0.875rem;
          font-weight: 400;
          color: #141414; }
        .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt .fiscal-submit {
          margin-left: 1.75rem;
          margin-top: 1.125rem;
          margin-bottom: 1.125rem; }
          .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt .fiscal-submit input {
            padding: 0.125rem 0;
            width: 4.25rem;
            font-size: 0.875rem;
            font-weight: 400;
            color: #141414;
            background-color: #1890ff;
            color: #ffffff; }
        .fiscal-box .fiscal-body .fiscal-query .fiscal-contxt iframe {
          display: none; }
    .fiscal-box .fiscal-body .fiscal-query::after {
      position: absolute;
      top: -0.625rem;
      left: 1em;
      content: '上海各园区年财政支持查询';
      font-size: 0.8125rem;
      font-weight: 500;
      color: #434343;
      background-color: #ffffff;
      padding: 0 1em; }
    .fiscal-box .fiscal-body .fiscal-query:last-child {
      margin-left: 1.125rem; }
    .fiscal-box .fiscal-body .fiscal-query:last-child::after {
      content: '浙江舟山自贸区财政扶持'; }
